.gallery__wrapper {
  width: 280px;
  margin: 0 auto;

  @media (min-width: $tablet-width) {
    width: 648px;
  }

  @media (min-width: $desktop-width) {
    width: 100%;
  }
}

.gallery__header {
  position: relative;
  width: 290px;
  margin: 0 auto;
  //PixelPerfect
  padding-bottom: 17px;
  margin-top: 33px;

  &::after {
    position: absolute;
    content: "";
    background-image: url("../img/line.svg");
    background-repeat: no-repeat;
    background-position: 0 0;
    width: 320px;
    height: 1px;
    bottom: 0;
    left: -20px;
  }
}

@media (min-width: $tablet-width) {
  .gallery__header {
    width: 458px;
    //PixelPerfect
    margin-top: 65px;
    margin-bottom: 46px;

    &::after {
      background-image: none;
    }
  }
}

@media (min-width: $desktop-width) {
  .gallery__header {
    margin-top: 103px;
    padding-bottom: 19px;
  }
}

.gallery__title {
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  //PixelPerfect
  margin-left: -6px;
  margin-bottom: 28px;

  @media (min-width: $tablet-width) {
    font-size: 30px;
    //PixelPerfect
    margin-top: 1px;
    margin-left: 1px;
    margin-bottom: 32px;
  }

  @media (min-width: $desktop-width) {
    //PixelPerfect
    margin-left: 0;
  }
}

.gallery__text {
  width: 275px;
  font-size: 14px;
  line-height: 24px;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  color: $darkgray;
  //PixelPerfect
  margin-left: 4px;

  @media (min-width: $tablet-width) {
    width: 430px;
    //PixelPerfect
    margin-left: 14px;
  }
}

.gallery__list {
  display: flex;
  flex-direction: column;
  margin: 0 auto;

  @include reset-list;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    flex-wrap: wrap;
  }

  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
  }
}

.gallery__item {
  margin-top: 22px;
  position: relative;
  padding-bottom: 15px;

  &::after {
    position: absolute;
    content: "";
    background-image: url("../img/line.svg");
    background-repeat: no-repeat;
    background-position: 0 0;
    width: 320px;
    height: 1px;
    bottom: 0;
    left: -20px;
  }
}

@media (min-width: $tablet-width) {
  .gallery__item {
    margin: 0;
    padding: 0;

    &::after {
      background-image: none;
    }
  }
}

// Стилизация фотографий под каждый экран
@media (min-width: $tablet-width) {
  .gallery__item--sunset {
    width: 648px;
  }

  .gallery__item--cactus {
    width: 324px;
  }

  .gallery__item--road {
    width: 324px;
  }

  .gallery__item--bridge {
    width: 324px;
  }
}

@media (min-width: $desktop-width) {
  .gallery__item--sunset {
    display: grid;
    grid-column: 1/4;
    grid-row: 1/2;
    width: 100%;
  }

  .gallery__item--cactus {
    display: grid;
    grid-column: 1/2;
    grid-row: 2/3;
    width: 100%;
  }

  .gallery__item--road {
    display: grid;
    grid-column: 2/3;
    grid-row: 2/3;
    width: 100%;
  }

  .gallery__item--bridge {
    display: grid;
    grid-column: 3/4;
    grid-row: 2/3;
    width: 100%;
  }
}

.gallery__name {
  font-weight: bold;
  font-size: 14px;
  line-height: 24px;
  align-items: center;
  text-transform: uppercase;
  //PixelPerfect
  margin-top: 5px;
  margin-bottom: 0;

  @media (min-width: $tablet-width) {
    display: grid;
    grid-column: 1/2;
    grid-row: 1/2;
    //PixelPerfect
    padding-top: 15px;
    padding-left: 20px;
    margin-top: 0;
  }

  @media (min-width: $desktop-width) {
    //PixelPerfect
    padding-left: 22px;
  }
}

.gallery__author {
  font-size: 14px;
  line-height: 24px;
  align-items: center;
  color: $darkgray;
  //PixelPerfect
  margin-top: 1px;
  margin-bottom: 0;

  @media (min-width: $tablet-width) {
    display: grid;
    grid-column: 1/2;
    grid-row: 2/3;
    padding-left: 20px;
    margin-top: 0;
    margin-bottom: 15px;
  }

  @media (min-width: $desktop-width) {
    color: $white;
    //PixelPerfect
    padding-left: 22px;
  }
}

.gallery__likes {
  font-weight: bold;
  font-size: 14px;
  line-height: 24px;
  align-items: center;
  text-transform: uppercase;
  position: relative;
  //PixelPerfect
  margin-top: 13px;
  margin-bottom: 0;
  padding-left: 37px;

  &::before {
    position: absolute;
    content: "";
    background-image: url("../img/like-icon.svg");
    background-repeat: no-repeat;
    background-position: 0 0;
    width: 23px;
    height: 21px;
    bottom: 6px;
    left: 0;
  }
}

@media (min-width: $tablet-width) {
  .gallery__likes {
    display: grid;
    grid-column: 2/3;
    grid-row: 1/3;
    //PixelPerfect
    margin-top: 4px;
    padding-left: 48px;

    &::before {
      top: 24px;
      left: 16px;
    }
  }
}

@media (min-width: $desktop-width) {
  .gallery__likes {
    &::before {
      background-image: url("../img/like-icon-desktop.svg");
    }
  }
}

//Стилизация описания картинок под каждый экран
@media (min-width: $tablet-width) {
  .gallery__description {
    display: grid;
    grid-template-columns: 1fr 100px;
    grid-template-rows: 1fr 1fr;
    min-width: 324px;
    min-height: 80px;
    margin: 0;
    background-color: $lightergray;
  }

  .gallery__description--road {
    background-color: $moregray;
  }
}

@media (min-width: $tablet-width) {
  .gallery__picture {
    display: block;
  }

  .gallery__picture--sunset {
    height: 400px;
  }

  .gallery__picture--cactus,
  .gallery__picture--road,
  .gallery__picture--bridge {
    height: 267px;
  }
}

@media (min-width: $desktop-width) {
  .gallery__photo {
    position: relative;
  }

  .gallery__photo--cactus,
  .gallery__photo--road,
  .gallery__photo--bridge {
    height: 267px;
  }

  .gallery__description {
    position: absolute;
    width: 100%;
    min-height: 80px;
    bottom: 0;
    left: 0;
    background-color: $blackopacity;
    color: $white;
  }
}

//PixelPerfect
@media (max-width: $mobile-width-only) {
  .gallery__item--cactus {
    margin-top: 23px;
  }

  .gallery__item--road {
    margin-top: 24px;
  }

  .gallery__description--road {
    margin-top: -1px;
  }

  .gallery__item--bridge {
    margin-top: 24px;
  }

  .gallery__description--bridge {
    margin-top: -1px;
  }
}
